import '../../static/common.css'
export default {
	data() {
		return {
			village_2: `${this.$env.img1}village_2.png`,
			longitude: '',
			latitude: '',
			pageIndex: 1,
			pageSize: 10,
			voteInfo: {},
			modalName: null,
			radio: 'radio1',
			cuIcon: [{
				name: 'appreciate',
				isShow: true
			}, {
				name: 'check',
				isShow: true
			}],
			bannerPic: "",
			indexInfo: {},
			msgInfo: {}
		};
	},
	methods: {
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		RadioChange(e) {
			this.radio = e.detail.value
		},
		villageClick() {
			console.log('他乡')
		},
		scroll: function(e) {
			console.log(e)
			this.old.scrollTop = e.detail.scrollTop
		},
		nameSearch(){
			uni.navigateTo({
				url: "../serch/serch"
			})
		},

		// 查看魅力值说明
		msgInit() {
			let that = this
			that.$request.phpTokenRequest(that.$api.GetDocument, that.$api.get, {
				Key: that.$api.Key,
				Client: that.$api.CLIENT,
				Version: that.$api.VERSION,
				Type: 37
			}).then((res) => {
				if (res.data.flag === 200) {
					that.msgInfo = res.data.data
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					});
				}
			});
		},
		//搜索框
		searchIcon(e) {
			let key = e.detail.value.toLowerCase();
			let list = this.cuIcon;
			for (let i = 0; i < list.length; i++) {
				let a = key;
				let b = list[i].name.toLowerCase();
				if (b.search(a) != -1) {
					list[i].isShow = true
				} else {
					list[i].isShow = false
				}
			}
			this.cuIcon = list
		},
		upper: function(e) {
			console.log(e)
		},
		lower: function(e) {
			console.log(e)
		},
		//获取经纬度 ，获取区域id
		getLocation(e) {
			let that = this;
			uni.getLocation({
				type: 'gcj02',
				success: function(res) {
					that.latitude = res.latitude;
					that.longitude = res.longitude;
					that.$request.httpTokenRequest(that.$api.currentCity, that.$api.get, {
						latitude: that.latitude,
						longitude: that.longitude,
						key: that.$api.Key,
						Client: that.$api.CLIENT,
						Version: that.$api.VERSION,
						searchType: 1,
					}).then((res) => {
						if (res.data.flag === 200) {
							that.getVoteList(that.latitude, that.longitude)
						} else {}
					});
				}
			});
		},
		//投票列表接口
		getVoteList(latitude, longitude) {
			let that = this
			that.$request.httpTokenRequest(that.$api.getSmallProceduresIndex, that.$api.get, {
				latitude: latitude,
				longitude: longitude,
				Key: that.$api.Key,
				Client: that.$api.CLIENT,
				Version: that.$api.VERSION,
				pageIndex: that.pageIndex,
				pageSize: that.pageSize,
			}).then((res) => {
				console.log(res.data.data)
				if (res.data.flag === 200) {
					that.voteInfo = res.data.data
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					});
				}
			});
		},
		//进入本村
		enterVillage(id){
			uni.navigateTo({
				url: "../details/details?villageId=" + id + "&longitude=" + this.longitude + "&latitude=" + this.latitude
			})
		},
		//投票
		voteClick(id) {
			// uni.showModal({
			// 	// title: '提示',
			// 	content: '请确认是否给该村投票',
			// 	success: function(res) {
			// 		if (res.confirm) {
			// 			this.votes(id)
			// 		} else if (res.cancel) {
			// 			console.log('用户点击取消');
			// 		}
			// 	}
			// });
			this.votes(id)

		},
		//点击投票
		votes(id) {
			this.$request.httpTokenRequest(this.$api.addVillageVoteNum, this.$api.get, {
				Key: this.$api.Key,
				Client: this.$api.CLIENT,
				Version: this.$api.VERSION,
				latitude: this.latitude,
				longitude: this.longitude,
				areaId: id,
			}).then((res) => {
				console.log(res)
				if (res.data.flag === 200) {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					});
					this.getLocation()
				} else {
					uni.showToast({
						title: res.data.data,
						icon: 'none'
					});
				}
			});
		},

	},
	onLoad() {
		this.userInfo=uni.getStorageSync('userInfo');
		this.$api.Key=this.userInfo.key
		this.getLocation()
		this.msgInit()
	},

}
